import React from 'react';

import Tooltip from './Tooltip';
import guy from './assets/guydebord.gif';

const Chapter2 = ({setShowContent}) => {
  return (
    <section className="Chapter">
      <div className="content-header">
        <h3 className="content-header-subtitle">Parte 2</h3>
        <h2 className="content-header-title">El autorregistro como espectáculo</h2>
      </div>
      <p className="content-paragraph">
      <span className="p-div float-left">
        <img alt="Guy debord" src={guy}/>
        <span>#GuyDebord #instamood</span>
      </span>
      <Tooltip content="Guy Debord, La sociedad del espectáculo (Santiago de Chile: Naufragio, 1995)."><span className="ref">Guy Debord</span></Tooltip>, el escritor, filósofo y cineasta líder de la vanguardia Situacionista del siglo XX afirmaba, en la <strong>Sociedad del Espectáculo</strong>, que “en el mundo realmente invertido, lo verdadero es un momento de lo falso”<Tooltip content="Debord,10"><span className="ref">*</span></Tooltip>. Con esto quería decir que, en el mundo contemporáneo, las imágenes del cine, la publicidad, la tv, y ahora la internet, no tratan de parecerse al mundo real, sino que somos nosotros, las personas en general, quienes intentamos hacer que lo real se parezca a las imágenes; en eso consiste el espectáculo. Para Debord, una manifestación de la <strong>falsa consciencia</strong> —el engaño compartido por todos de creer que es uno, en contraposición a los demás, quien puede triunfar en el sistema capitalista— es la aceptación irreflexiva del espectáculo. Queremos ser como la pieza publicitaria, como la comedia romántica o como la influencer, y los demás momentos monótonos de la vida son solo un hiato en esa pequeña espectacularidad que podemos mostrar en nuestras propias representaciones, por ejemplo, por medio del autorregistro. Pero <strong>es a través del consumo como se alimenta el espectáculo</strong>, pues además de comprar objetos de uso, se compran elementos que refuerzan las identidades públicas autoficcionadas, las mercancías de identidad. Para Debord, la Sociedad del espectáculo es entonces la negación de la vida, “la afirmación de toda vida humana, es decir social, como simple apariencia”<Tooltip content="Debord,10"><span className="ref">*</span></Tooltip>.
      </p>
      <blockquote>
      en el mundo realmente invertido, lo verdadero es un momento de lo falso
      </blockquote>
      <cite>— Guy Debord</cite>
      <p className="content-paragraph">
      El filósofo <Tooltip content="Jean Baudrillard, Critica de la economía política del signo (México: Siglo Veintiuno, 1991)."><span className="ref">Jean Baudrillard</span></Tooltip>, en Crítica de la economía política del signo, consideraba que, además del <strong>valor de uso</strong> —es decir, la forma en la que las cosas suplen alguna necesidad: la capacidad del martillo de clavar una puntilla— y el <strong>valor de cambio</strong> —es decir, la equivalencia que podemos determinar entre los objetos: dos puntillas que valen lo mismo, o el precio en dinero del martillo—, existen dos formas adicionales de valor: <strong>el valor simbólico</strong> y <strong>el valor/signo</strong>.
      </p>
      <p className="content-paragraph">
      El <strong>valor simbólico</strong>, aunque Baudrillard no lo explicaría con los mismos términos, es la relación de empatía que surge con el interrelacionamiento social. El ejemplo más claro es el de un regalo: supongamos que un niño le regala a su mamá una flor de diente de león que se encuentra por la calle. En términos de valor de uso, el diente de león no sirve para mucho —tal vez para hacer un agua de hierbas o para ponerlo en un florero—, y en términos de valor de cambio el precio es casi nulo, porque, al ser maleza, el diente de león es sobreabundante y se puede encontrar en cualquier resquicio que contenga tierra en los andenes de la calle. Desde el punto de vista de Baudrillard, el principal valor de ese diente de león en particular sería simbólico, porque de alguna forma representa el amor que siente el niño por su mamá; es una forma de empatía social manifestada en un objeto.
      </p>
      <p className="content-paragraph">
      Por el contrario, el <strong>valor/signo</strong> representa, no la empatía, sino la diferencia. El valor/signo está dado por el consumo que se realiza con la intención de verse como diferente de los demás. Para Baudrillard, el consumo o el gasto se relacionan con la destrucción, pues cuando se consume se reemplaza el uso por el prestigio; gastar quiere decir convertir el valor de cambio en algo inservible, en hacer un sacrificio que me pone por encima de los demás —paradójicamente, si los demás así lo reconocen—. De hecho, el valor/signo es una forma de diferenciación agonística, porque se produce entre miembros de la misma clase social. Luego de la acumulación de capital, sigue la acumulación de prestigio.
      </p>
      <p className="content-paragraph">
      Pongamos por ejemplo un anillo de bodas: supongamos que dos parejas se van a casar, y una, que es muy pobre, solo tiene para regalarse anillos de alambre dulce. La otra pareja, que es muy rica, por su parte, tiene dinero suficiente para regalarse anillos de diamantes. En términos de valor de uso, ambos anillos sirven para lo mismo: realmente para muy poco. En términos de valor de cambio, en cambio, la diferencia es abismal: los anillos de alambre no valen nada, los de diamantes valen mucho. El valor simbólico en los dos anillos es el mismo. No podríamos decir que una pareja se quiere más que la otra por haber pagado más por los anillos. En contraste, el valor/signo es evidente: la pareja que consume más, que sacrifica más dinero, obtiene más prestigio; esa es justamente toda la gracia de los diamantes.
      </p>
      <blockquote className="no-quotes">
      valor de uso —&gt; necesidad
      <br/>
      valor de cambio —&gt; equivalencia
      <br/>
      valor simbólico —&gt; relacionamiento
      <br/>
      valor/signo —&gt; diferencia
      </blockquote>
      <p className="content-paragraph">
      El fenómeno del autorregistro juega con estas formas de valor: el movimiento ambivalente entre pertenecer a cierta clase social —ser simbólicamente igual a los demás— y a la vez competir agonísticamente por prestigio –ser simultáneamente mejor y diferente—. La presunción y la ostentación de la vida privada en la esfera pública es registrada minuciosamente y luego es vendida de vuelta. La diferencia entre el colonialismo típico y el colonialismo de los datos está en que la extracción de la materia prima se hace bajo la ilusión de lo voluntario, en la participación en falsa consciencia del espectáculo. Ahora podemos preguntarnos, ¿qué se hace con los datos de nuestro autorregistro?
      </p>
      <div className="continue-button-div">
        <button onClick={()=>{window.scrollTo(0, 0);setShowContent(d=>d-1)}} className="default-button">Atrás</button>
        <button onClick={()=>{window.scrollTo(0, 0);setShowContent(d=>d+1)}} className="default-button">Continuar</button>
      </div>
    </section>
  );
}

export default Chapter2;

