import React from 'react';

import Tooltip from './Tooltip'
import Regression from './Regression'
import NearestN from './NearestN'

const Chapter3 = ({setShowContent, userData, dimensions}) => {
  return (
    <section className="Chapter">
      <div className="content-header">
        <h3 className="content-header-subtitle">Parte 3</h3>
        <h2 className="content-header-title">Predicción y clasificación</h2>
      </div>
      <p className="content-paragraph">
      Los agregados de datos, producto del autorregistro, no son útiles en sí mismos para las industrias tecnológicas, así como una materia prima no sirve de nada si no se transforma. Los sistemas de inteligencia artificial, llamados “aprendizaje automático” —o machine learning, en inglés— que se han expandido rápidamente junto con la Web 2.0, han permitido crear formas muy sofisticadas de procesamiento de los datos personales. No obstante, y en general, tal procesamiento puede reducirse a dos procesos similares: es posible hacer <strong>predicciones numéricas</strong> con ciertos tipos de datos y es posible crear <strong>clasificaciones</strong> con algunos otros.
      </p>
      <p className="content-paragraph">
      Las <strong>predicciones</strong> son cálculos matemáticos que buscan proyectar tendencias o reglas desde datos numéricos conocidos a datos numéricos desconocidos. Por ejemplo, supongamos que sabemos el número de pares de medias de algunas personas y qué tan felices son, de 0 a 10. ¿Cómo saber cuál sería tu índice de felicidad si no lo conociéramos aún? Podríamos hacer lo que en estadística y aprendizaje de datos llaman una regresión lineal, es decir, crear un modelo que implica trazar una línea que represente la tendencia de nuevos valores de acuerdo con los valores conocidos.
      </p>
      <p className="content-paragraph">
      Prueba la regresión lineal con la siguiente gráfica interactiva. Los valores conocidos son los círculos blancos, la línea roja representa la predicción de los valores desconocidos, y el punto azul sería tu índice de felicidad en la predicción. Prueba pasar el cursor por los puntos para ver el índice de cada persona, y haz clic en la gráfica para crear nuevos puntos y cambiar la predicción (y, en consecuencia, el emoji).
      </p>
      <Regression userData={userData} dimensions={dimensions} />
      <p className="content-paragraph">
      Las <strong>clasificaciones</strong>, por su parte, son también cálculos matemáticos, pero no buscan crear predicciones numéricas sino establecer grupos de elementos similares. En otras palabras, sirven para agrupar objetos o personas en categorías delimitadas. Por ejemplo, este sistema de clasificación agrupa a las personas de acuerdo con su edad y color preferido, y les asigna una etiqueta valorativa (bueno, regular o malo). Haz clic en el botón de "agrupar" para hacer la clasificación (puede tardar unos segundos). ¿Qué te tocó a tí?
      </p>
      <NearestN userData={userData} dimensions={dimensions} />
      <p className="content-paragraph">
      Aunque los sistemas de predicción y clasificación de datos están basados en complejos cálculos matemáticos, y eso les brinda cierta apariencia de objetividad y de fiabilidad estadística, podríamos releer su éxito de acuerdo con la lógica de la datificación y la sociedad de espectáculo: <strong>no son, al final, los modelos los que procuran parecerse a los datos que registran, son los datos los que procuran parecerse a los modelos</strong>. Debord diría que el espectáculo es “una actividad especializada que habla por el conjunto de las otras”<Tooltip content="Debord, La sociedad del espectáculo, 14"><span className="ref">*</span></Tooltip>. El constante autorregistro hace que nuestras vidas, para que funcionen dentro del sistema del prestigio, se acomoden a los modelos de clasificación de la inteligencia artificial; es un círculo vicioso. En buena medida, los modelos funcionan porque amoldan nuestras vidas: las enclasan, las enrazan, las generizan, las conectan con estilos de vida, con formas de ser, con identidades públicas, las transforman para que luego sean predecibles y clasificables dentro de la propia lógica del sistema que las produjo. La inconsistencia y la vaguedad se encajonan en categorías computacionalmente manipulables, y reemplazan a lo real. Pero, ¿de qué sirve ese encajonamiento?
      </p>
      <div className="continue-button-div">
        <button onClick={()=>{window.scrollTo(0, 0);setShowContent(d=>d-1)}} className="default-button">Atrás</button>
        <button onClick={()=>{window.scrollTo(0, 0);setShowContent(d=>d+1)}} className="default-button">Continuar</button>
      </div>
    </section>
  );
}

export default Chapter3;