import React from 'react';

import Tooltip from './Tooltip';
import Histogram from './Histogram';

const Chapter5 = ({setShowContent, userData, dimensions}) => {
  return (
    <section className="Chapter">
      <div className="content-header">
        <h3 className="content-header-subtitle">Parte 5</h3>
        <h2 className="content-header-title">Sesgo e invisibilización</h2>
      </div>
      <p className="content-paragraph">
      Como afirman <Tooltip content="Geoffrey C. Bowker y Susan Leigh Star, Sorting Things out: Classification and Its Consequences, First paperback edition, Inside Technology (Cambridge, Massachusetts London, England: The MIT Press, 2000)."><span className="ref">Bowker y Star</span></Tooltip>, los sistemas de clasificación siempre dejan cosas por fuera, y en raras ocasiones funcionan perfectamente como se diseñaron. Existe una clara contradicción entre el mundo idealizado de los estándares que buscan protocolizar los datos acerca del mundo y el propio mundo que no se ajusta a esos protocolos. Un sistema de clasificación, en principio, debería ser consistente y evitar categorías que se superponen, pero la vida real es justamente inconsistente y sobrepuesta. ¿Debería tener un color preferido? ¿Por qué no podría tener diez o veinte? ¿Debería preferirlos todos en todo momento? ¿Por qué no podría cambiar de gusto de acuerdo con mi estado de ánimo, con el momento del día, con las experiencias previas? ¿Podría mi color preferido ser, a veces, mi color más odiado?
      </p>
      <p className="content-paragraph">
      Un caso claro en el que se manifiestan las limitaciones de los sistemas de predicción y clasificación automática es lo que en estadística llaman <strong>“casos atípicos” —o outliers, en inglés—</strong>. Un caso atípico es un objeto o un fenómeno que está muy alejado de las cualidades del grupo al que aparentemente pertenece. Por ejemplo, una persona que gana mucho dinero es un caso atípico, aunque la falsa consciencia nos haga creer que esa persona podría ser cada uno de nosotros —y no los demás—. Pero también es un caso atípico una persona que no se conforma con las reglas que se le han impuesto, o una persona que no había sido contemplada dentro de un modelo descriptivo. <strong>Los casos atípicos distorsionan las predicciones y suelen ser invisibilizados o forzados a encajar dentro de una categoría en las clasificaciones, porque los modelos predictivos y de clasificación suelen buscar las reglas que siempre se repiten, los rasgos generales, lo más común</strong>. Como los datos atípicos distorsionan, en los modelos estadísticos se suelen eliminar o minimizar para que el sistema se comporte mejor; sin embargo, en la vida real es justamente necesario que se consideren los casos atípicos con mayor atención, no que se eliminen: pensemos en las condiciones de accesibilidad que deben tener los edificios públicos para que personas que no pueden subir escaleras de todas formas puedan circular libremente. Si no existieran esas condiciones de accesibilidad, se estaría creando un lugar apto para la mayoría, pero se le estaría haciendo la vida imposible a personas con capacidades y cualidades diferentes.
      </p>
      <p className="content-paragraph">
      Para ver esto más claramente usemos la siguiente gráfica. Supongamos que representa la frecuencia de los colores que más les gustan a cien personas, incluyéndote a ti (tu lugar estaría en la barra con borde rojo). En este caso, serías un caso atípico, porque tu gusto no correspondería con el de la mayoría de la gente, así que serías un elemento problemático para un sistema de predicción o clasificación.
      </p>
      <Histogram
        userData={userData}
        dimensions={dimensions}
      />
      <p className="content-paragraph">
      Otra limitación de los sistemas de aprendizaje automático es la introducción de <strong>sesgos</strong>. La mayoría de las veces, los sistemas de predicción y clasificación se basan en datos tangenciales al problema en cuestión, los llamados <strong><em>proxies</em>, o aproximaciones</strong>. Un <em>proxy</em> es una especulación que se deriva, no de conocer las causas y efectos concretos de un problema sino de, por decirlo de alguna manera, tener a la mano síntomas que coocurren con lo que se quiere investigar. ¿Qué tendría que ver la cantidad de medias con la felicidad? ¿Habría otra manera más clara de saber si alguien es feliz? 
      </p>
      <p className="content-paragraph">
      <span className="p-div float-left">
        <a target="_blank" rel="noreferrer" href="https://en.wikipedia.org/wiki/Physiognomy#/media/File:Porta59.jpg"><img alt="De humana physiognomonia" src="https://upload.wikimedia.org/wikipedia/commons/d/d7/Porta59.jpg"/></a>
        <span>Giambattista Della Porta — <em>De humana physiognomonia</em> — 1586</span>
      </span>
      Como hemos insistido, la datificación está construida sobre la ficción autobiográfica, no sobre la vida real, y como tal, es una manifestación de la sociedad del espectáculo y del triunfo de la apariencia: el signo como remplazo del significado. <strong>Como los sistemas de predicción y clasificación están basados en apariencias, en ocasiones pueden parecerse a las pseudociencias que se atribuyen capacidades teóricas desde información dudosa</strong>. Pensemos en la frenología, esa supuesta ciencia del siglo XIX que afirmaba poder establecer el carácter y la personalidad de los seres humanos basándose en la forma del cráneo, o la fisionomía, que aparentemente podía definir la moralidad de alguien según sus facciones. Por ejemplo, sobre la criminología fisionómica, <Tooltip content="Stefania Milan y Emiliano Treré, «Big Data from the South(s): Beyond Data Universalism», Television & New Media 20, n.o 4 (mayo de 2019): 319-35, https://doi.org/10.1177/1527476419837739."><span className="ref">Siegfired Zielinski</span></Tooltip> diría: “La superposición de varios y distintos retratos servía, supuestamente, para lograr distinguir la caracterización de distintos tipos de criminales. El procedimiento de composición recibió el estatus de un método con el cual se logró definir conceptualmente las tipologías del criminal. Eran estadísticas traducidas en imágenes”<Tooltip content="Zielinski, 297."><span className="ref">*</span></Tooltip>. Hoy, aunque no siempre lo aplicamos, sabemos que la anchura de la nariz de una persona no determina su tendencia a realizar actos inmorales, y, sin embargo, esperamos que los sistemas digitales del aprendizaje automático definan quienes somos a partir de datos superficiales —que, en el caso de la “visión por computadora”, perfectamente podrían seguir estando relacionados con el ancho de la nariz—. Al respecto, <Tooltip content="Payal Arora, «Bottom of the Data Pyramid: Big Data and the Global South», International Journal of Communication; Vol 10 (2016), 14 de marzo de 2016, https://ijoc.org/index.php/ijoc/article/view/4297."><span className="ref">Payal Arora</span></Tooltip> afirma: “en la datificación del cuerpo, hay una suposición innata de armonía entre el estado, los datos y el cuerpo. Esta mitificación de la armonía es una seria barrera a la práctica y las políticas consideradas y reflexivas”<Tooltip content="Arora, 1685."><span className="ref">*</span></Tooltip>. <strong>La suposiciones pseudocientíficas de la apariencia han servido, más que todo, para construir prejuicios sociales, y para forzar a las personas a marginalizarse y a convencerse de que su lugar es esa marginalidad, porque no se adaptan al sistema cuando es, por el contrario, el sistema el que se debería adaptar a ellas</strong>.
      </p>
      <p className="content-paragraph">
      <span className="p-div float-left">
        <a target="_blank" rel="noreferrer" href="http://karpathy.github.io/assets/selfie/grid_render_best.jpg"><img src="http://karpathy.github.io/assets/selfie/grid_render_best.jpg" alt="Las cien mejores fotos según el estudio de Karpathy"/></a>
        <span>Las cien mejores selfies en el estudio de Karpathy</span>
      </span>
      Por supuesto, en cierto sentido esta es una caricatura de teorías matemáticas que pueden llegar a ser muy sólidas, como la estadística, pero, el problema no está tanto en que las teorías sean incorrectas como en su incorrecta aplicación sin considerar el contexto y las causas subyacentes. Para <Tooltip content="Stefania Milan y Emiliano Treré, «Big Data from the South(s): Beyond Data Universalism», Television & New Media 20, n.o 4 (mayo de 2019): 319-35, https://doi.org/10.1177/1527476419837739."><span className="ref">Milán y Treré</span></Tooltip>: “el principal problema con el universalismo de los datos es que es asocial y ahistórico, pues presenta a la tecnología (y las dinámicas relacionadas con la datificación, añadimos) como algo que opera por fuera de la historia y contextos sociopolíticos, culturales y económicos específicos”<Tooltip content="Milan y Treré, 325."><span className="ref">*</span></Tooltip>. Por ejemplo, Andrej Karpathy, el científico de la computación director de la sección de inteligencia artificial para piloto automático de la compañía de carros Tesla, en el 2015 hizo <a target="_blank" rel="noreferrer" href="http://karpathy.github.io/2015/10/25/selfie/" className="ref">un experimento</a> en el que trataba de predecir y categorizar, por medio de “redes neuronales artificiales”, cuáles eran las cualidades de las mejores selfies en Instagram. Como no hay un indicador de cuál foto es mejor, Karpathy usó la cantidad de <em>likes</em> que tenía cada foto como medida de calidad. Justo aquí podríamos preguntarnos si calidad es equivalente a popularidad; o al menos si el número de <em>likes</em> es un <em>proxy</em> suficientemente bueno. Luego de crear su sistema de inteligencia artificial, Karpathy descubrió que las “mejores” fotos seguían un patrón muy claro: representaban a mujeres blancas de cabello liso y largo, con filtros y con encuadres cercanos que cortaban la frente. Aquí, aunque el sistema computacional funcionaba bien (hacía justo lo que le ordenaban), el problema estaba en la forma en la que el ser humano que creó el sistema había conceptualizado el propósito de una forma que necesariamente introduciría sesgos.
      </p>
      <div className="continue-button-div">
        <button onClick={()=>{window.scrollTo(0, 0);setShowContent(d=>d-1)}} className="default-button">Atrás</button>
        <button onClick={()=>{window.scrollTo(0, 0);setShowContent(d=>d+1)}} className="default-button">Continuar</button>
      </div>
    </section>
  );
}

export default Chapter5;