import React from 'react';

import Tooltip from './Tooltip';
import honneur from './assets/honneur.jpg';

const Chapter1 = ({setShowContent}) => {
  return (
    <section className="Chapter">
      <div className="content-header">
        <h3 className="content-header-subtitle">Parte 1</h3>
        <h2 className="content-header-title">Introducción</h2>
      </div>
      <p className="content-paragraph">
      Estamos acostumbrados a vivir una vida datificada. Si <Tooltip content="Marshall McLuhan, Understanding media: the extensions of man Cambridge, Mass: MIT Press, 1994)."><span className="ref">Marshall McLuhan</span></Tooltip> decía que la luz eléctrica ayudó a extender las posibles horas de trabajo —a iluminar la noche—, y que el reloj ayudó a regular y puntuar las tareas cotidianas en un régimen minucioso —a darle a cada acción un momento del día—, ahora podemos afirmar que la publicación de nuestras vidas en redes sociales regula nuestra <strong>ficción autobiográfica</strong>: la línea del tiempo y las cualidades de nuestra identidad pública.
      </p>
      <p className="content-paragraph">
      La vida cotidiana contemporánea está dada por un constante <strong>autorregistro</strong>: qué comemos, qué decimos, qué creemos que nos gusta, a dónde vamos, con quién nos relacionamos, en qué lugar del espectro político nos ubicamos. Conscientes de ello o no, cada vez que subimos una foto, damos un <em>like</em>, compartimos una noticia, saltamos una canción, buscamos una ruta, opinamos virtualmente, etc., estamos creando un modelo predictivo acerca de nuestras supuestas formas de pensar y de actuar, y estamos categorizándonos dentro de sistemas que coleccionan aparentes patrones regulares en las vidas de las personas. <strong>El autorregistro es una rutina puntuada que consiste en realizar pequeñas tareas de datificación constantemente, un <em>tic toc</em> mecanizado que se intercala rítmicamente con el reloj y el interruptor del bombillo, la construcción monótona de una ficción autobiográfica interesante a partir de la transformación de la vida en datos operables por computadores.</strong>
      </p>
      <blockquote>
      Las identidades en la experiencia vivida son difusas, inconsistentes, contradictorias; las identidades ficcionadas de la datificación procuran ser estables, porque tienen como finalidad la predicción y el etiquetado.
      </blockquote>
      <p className="content-paragraph">
      Esta ficción autobiográfica, alimentada por el constante autorregistro, es ilusoria, pues es una identidad que está dada más que todo por el consumo. <strong>Qué consumimos se confunde con qué somos</strong>. Llenar un perfil en una red social puede ser visto como una delimitación de qué hemos comprado o qué podríamos comprar más que un retrato esencial de nuestra existencia. Las identidades en la experiencia vivida son difusas, inconsistentes, contradictorias; las identidades ficcionadas de la datificación procuran ser estables, porque tienen como finalidad la predicción y el etiquetado. No obstante, a pesar de la contradicción de la vida real, <strong>romper con la identidad ficcionada se siente como una traición al propio ser, como si la ficción autobiográfica fuera un compromiso que se asume para siempre</strong>, y resulta ofensivo que se cuestione esa identidad. <Tooltip content="Bethan Benwell y Elizabeth Stokoe, Discourse and identity (Edinburgh: Edinburgh University Press, 2006)."><span className="ref">Benwell y Stokoe</span></Tooltip> afirman que la identidad, que antes estaba confinada a la idea de la esencia interna y subjetiva, ha migrado al discurso público; la identidad es la forma como los demás me ven, las categorías que usan para clasificarme, y mi lugar en ese aparataje identitario está dado por cómo construyo un discurso que los demás puedan interpretar por intermediación computacional.
      </p>
      <p className="content-paragraph">
      Para <Tooltip content="Domenico Fiormonte, Teresa Numerico, y Francesca Tomasi, The Digital Humanist: A Critical Inquiry (Brooklyn: punctum books, 2015)."><span className="ref">Fiormonte, Numerico y Tomasi</span></Tooltip> esta lógica de la datificación logró tener la propagación actual con el establecimiento del concepto pegajoso de la <strong>Web 2.0</strong>, inventado por el empresario y editor Tim O’Reilly. La Web 2.0 tuvo lugar con la ampliación de los medios que pueden circular en internet; ya no solo documentos de texto, sino también imágenes, video, sonido, realidad aumentada y expandida. Es difícil imaginar o recordar un momento en el que no existía la abundancia mediática del presente. Si lo leemos con los lentes del arqueólogo de los medios <Tooltip content="Friedrich A. Kittler, Gramophone, Film, Typewriter, Writing Science (Stanford, Calif: Stanford University Press, 1999)."><span className="ref">Friedrich Kittler</span></Tooltip>, podríamos decir que la Web 2.0 trae consigo la consolidación definitiva de la internet, alejada de sus orígenes militares, en un medio del entretenimiento, con todo su acervo audiovisual. Con la ampliación de la web, según como se mire, llegó la apertura democrática de la creación y consumo de contenido por parte de cualquier persona, o la transformación de la infraestructura de la internet en una plataforma para la explotación capitalista de los contenidos y los datos de todos por parte de unos pocos. Todos podemos crear en la Web 2.0, siempre que tengamos acceso a la red. Todos podemos participar en la producción de esa forma de expresión genérica que llamamos “contenido”, pero ¿quién realmente le saca provecho?, ¿quién mantendría esa infraestructura de la comunicación desinteresadamente?, y, más importante aún, ¿quien querría igualar identidad y consumo?
      </p>
      <p className="content-paragraph">
      <span className="p-div float-left">
        <a target="_blank" rel="noreferrer" href="https://iiif.lib.harvard.edu/manifests/view/ids:8945117"><img alt="Le Petit Journal — ¡Honor a los héroes de la expansión colonial! — 1910" src={honneur}/></a>
        <span>Le Petit Journal — ¡Honor a los héroes de la expansión colonial! — 1910</span>
      </span>
      <Tooltip content="Ulises Mejías y Nick Couldry, «Colonialismo de datos: repensando la relación de los datos masivos con el sujeto contemporáneo», Virtualis; Vol. 10, Núm. 18 (2019): Tecnopolítica disidente y cultura digital en América Latina, 20 de mayo de 2019."><span className="ref">Mejías y Couldry</span></Tooltip> entienden este proceso de la datificación, derivada del autorregistro, promovida por la democratización de contenido de la Web 2.0, como otra forma de colonialismo. <strong>Colonialismo de datos</strong>, dirían ellos: “las plataformas producen “lo social” para el capital, es decir, una versión de lo social que está lista para la apropiación y la explotación del valor en forma de datos”<Tooltip content="Mejías y Couldry, 3"><span className="ref">*</span></Tooltip>. Desde su punto de vista, los datos son como una materia prima, similar a los minerales o los alimentos básicos, extraídos o producidos con tareas no muy especializadas, pero sí muy mecánicas, por un grupo de personas reemplazables para la gran infraestructura tecnológica. Un dato más, un dato menos, no importa en el extenso acumulado del conjunto de datos. Lo que interesa, en esta escala, es la actividad extractiva de la clasificación de la vida. Pero, si esto es así, ¿por qué nos autoregistramos tan cándidamente?
      </p>
      <br/>
      <br/>
      <br/>
      <div className="continue-button-div">
        <button onClick={()=>{window.scrollTo(0, 0);setShowContent(d=>d+1)}} className="default-button">Continuar</button>
      </div>
    </section>
    
  );
}

export default Chapter1;

